* {
  text-transform: uppercase;
  box-sizing: border-box;
  --main-color: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 16px;
}

header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.logo-style {
  width: 40px;
  height: 40px;
  margin-bottom: -3px;
}

ul {
  margin: 0;
  display: flex;
}

li {
  margin-left: 20px;
  list-style: none;
}

ul li:first-child {
  margin-left: -40px;
}

ul li:last-child {
  margin-right: 0;
}

.element-style {
  line-height: 60px;
}

.link-style {
  text-wrap: nowrap;
  color: #000;
  text-decoration: none;
  transition: color .3s;
  display: inline-block;
  position: relative;
}

.link-style:hover {
  color: var(--main-color);
}

.link-style:after {
  content: "";
  background-color: var(--main-color);
  transform-origin: center;
  border-radius: 10px;
  width: 100%;
  height: 4px;
  transition: transform .3s;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(0);
}

.link-style:hover:after {
  transform: scaleX(1);
}

.is-active {
  color: var(--main-color);
}

.is-active:after {
  transform: scaleX(1);
}

[data-qa="hover"]:after {
  display: none;
}
